
.story {
  width: 100%;
  height: 100%;
}
.story .item {
  height: 140px;
  width: 400px;
  border: 1px solid #eee;
  float: left;
  margin: 10px;
}
.story .item a {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
}
.story .item a h4 {
  color: #f00;
  font-size: 20px;
  margin: 10px 0;
}
.story .item a p {
  line-height: 1.2;
  overflow: hidden;
  display: -webkit-box;
  word-break: break-all;
  -webkit-line-clamp: 5;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
}
.story .item a:hover {
  text-decoration: underline;
}
.story img {
  max-width: 140px;
  margin-right: 10px;
}


