
.music {
  height: 100%;
  width: 100%;
  overflow: hidden;
  position: relative;
  text-align: center;
}
.music img {
  height: 100%;
  width: 100%;
  -webkit-filter: blur(100px);
          filter: blur(100px);
  position: absolute;
  left: 0;
  top: 0;
}
.music .song {
  height: 500px;
  width: 800px;
  position: absolute;
  left: 50px;
  top: 100px;
  z-index: 99;
/*border: 1px solid red;*/
}
.music .song .msg {
  color: #d9d8d8;
  margin: 5px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}


