
.me {
  height: 100%;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: url("./bg.jpg") no-repeat;
  background-size: 100%;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.me .resume {
  height: 40px;
  width: 100px;
  font-size: 25px;
  color: #fff;
  text-align: center;
  line-height: 40px;
  display: inline-block;
  border-bottom: 1px solid #eee;
  -webkit-transition: all 1s;
  transition: all 1s;
}
.me .resume:hover {
  color: #008000;
  border-color: #008000;
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}


